import { createRouter, createWebHistory } from 'vue-router'; //createWebHashHistory,createWebHistory
import { _isMobile } from '@/utils/common';
import { pcRouter } from './pc';
import { h5Router } from './h5';
console.log(_isMobile());
const commonRouter = [];
// pc、h5 router config
const routes = _isMobile()
  ? [...commonRouter, ...h5Router]
  : [...commonRouter, ...pcRouter];
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  // history: createWebHashHistory(),
  routes
});
router.afterEach(() => {
  // go back top
  window.scrollTo(0, 0);
});
export default router;
